<script setup>
import { ref, reactive, onBeforeMount, watchEffect, onMounted } from 'vue';
import { getMarketSell } from '@/http/api';
import { commonStore } from '@/store/common';
import pixelItem from '@/components/pixelItem.vue';
import { useRouter } from 'vue-router';
const common_store = commonStore();
const data = reactive({
  list: [],
  counts: [],
  timer: null,
  time: 1,
  count: 0,
  time2: 0,
});
const dom = ref(null);
const getMarketList = () => {
  getMarketSell(0, 'selltime').then((res) => {
    data.list = res.data.results;
    data.count = res.data.count;
    const indexArr = [];
    data.list.forEach((item) => {
      indexArr.push(item.tokenid);
    });

    if (data.list.length > 4) {
      data.list = data.list.concat(res.data.results);
    }
  });
};
onBeforeMount(() => {
  getMarketList();
});
watchEffect(() => {
  if (common_store.address) {
    common_store.initMarketWeb3();
  }
});
const router = useRouter();
const toDetails = (item) => {
  if (common_store.address) {
    router.push({
      path: '/marketDetails',
      query: item,
    });
  } else {
    common_store.setLinkShow(true);
  }
};
let scrollWidth = ref(0);

const setTime = () => {
  data.time2++;
  data.time = data.time2 % (data.count * 2 * 60);
  if (data.time2 == data.count * 2 * 60) {
    data.time2 = 0;
  }
  requestAnimationFrame(setTime);
};
onMounted(() => {
  setTime();
  watchEffect(() => {
    scrollWidth.value = 354 * data.list.length;
  });
});
</script>

<template>
  <ul class="market-text center">
    <li class="Wcolor Bold market-text_title">{{ $t('Home.market1') }}</li>
    <li class="lightGrey font-Eight font400 market-text_text">
      {{ $t('Home.market2') }}
    </li>
    <li class="market-text_btn">
      <n-button type="primary" block @click="router.replace('/market')">{{
        $t('Home.text9')
      }}</n-button>
    </li>
  </ul>
  <div class="market-box">
    <div
      class="market-box_list"
      ref="dom"
      :style="{
        'transition-duration':
          data.time <= 120 ? '0s' : data.count * 2 - 2 + 's',
        transform:
          data.time <= 120
            ? 'translateX(0)'
            : `translateX(${-(scrollWidth / 2)}px)`,
      }"
    >
      <div
        v-for="(item, key) in data.list"
        :key="key"
        style="margin-left: 15px"
        class="market-box_item listBg"
      >
        <pixel-item
          :pixelInfo="item"
          name="market"
          type="home"
          class="item hand"
          :count="0"
          @click="toDetails(item)"
        ></pixel-item>
      </div>
    </div>
  </div>
</template>

