  width: 352px;
  border-radius: 10px;
  box-sizing: border-box;
  &_change {
    padding: 15px 10px;
    margin: 20px auto;
  }
}
.empowerBox {
  height: 600px;
}
.noempowerBox {
  height: 550px;
}

.color-title {
  height: 50px;
  line-height: 50px;
  position: relative;
}
.empower-btn {
  margin-top: 15px;
}
</style>
